.content {
    padding: 1rem;
    color: white;
    text-align: center;
    background-color: black;
}

.content h1 {
    margin-top: 1rem;
    animation: fade-in 7s;
}

.content h2 {
  margin-top: 1rem;
  animation: fade-in 7s;
}

.content-intro {
  animation: fade-in 7s;
  margin: 3rem 20%;
  line-height: 2;
  font-size: 1.5rem;

}

@keyframes fade-in {
    0% {opacity: 0;}
    50% {opacity: 50%;}
    100% {opacity: 100%;}
}

.cars-cont {
    position: relative;
    background-color: none;
    left: 50%;
    margin-top: 1rem;
    transform: translateX(-50%);
    animation: fade-in 7s;
}

.heart-cont {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    opacity: 0;
    animation: fade-in-out 5s;
}

@keyframes fade-in-out {
    0% {opacity: 0;}
    25% {opacity: 50%;}
    50% {opacity: 100%;}
    75% {opacity: 50%;}
    100% {opacity: 0;}

}

.heart {
    background-color: red;
    display: inline-block;
    height: 120px;
    margin: 0 10px;
    position: relative;
    top: 0;
    transform: rotate(-45deg);
    width: 120px;
  }
  
  .heart:before,
  .heart:after {
    content: "";
    background-color: red;
    border-radius: 50%;
    height: 120px;
    position: absolute;
    width: 120px;
  }
  
  .heart:before {
    top: -60px;
    left: 0;
  }
  
  .heart:after {
    left: 60px;
    top: 0;
  }

  .theBigAsk {
    margin-bottom: 2rem;
  }

  .question {
    border-radius: 5px;
    margin-top: 2rem;
    background-color: #eee8fb;
    color: black;
    padding: 1rem;
  }
  .the-question {
    font-size: 2rem;
  }

  .reveal-button {
    margin: 1rem;
    padding: 1rem;
    cursor: pointer;
    background-color: #eee8fb;
    border-radius: 5px;
    font-size: 1rem;
}

  @media only screen and (max-width: 768px) {
    .content-intro {
      background-color: grey;
      border-radius: 5px;
      padding: 1rem;
      line-height: 1.5;
    }
  }