.car-cont {
    position: relative;
    z-index: 10;
    width: 75%;
    left: 50%;
    margin-top: 5rem;
    margin-bottom: 5rem;
    transform: translateX(-50%);
}

.carousel {
    position: relative;
    margin-top: 2rem; 
    display: none;
}


.carousel-active {
    display: block;
}

.slider {
    animation: slideIn;
    animation-duration: 0.5s;
    transition: 0.5s;
    overflow: hidden;  
}

@keyframes slideIn {
    0% {opacity: 0;
    transform: scale(0.5);}
    100% {opacity: 1;
    transform: scale(1.0);}
}

.img-wrapper::after {
    background:linear-gradient(var(--cardinal), var(--mahogany));
    opacity: 50%;
    content: '';
    border-radius: 15px;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
}

.carousel img {
    width: 100%;
    height: 600px;
    border-radius: 15px;
    position: relative;
    opacity: 90%;
    filter: grayscale(100%);
}


.carousel h1 {
    position: absolute;
    left: 50%;
    top:10%;
    font-size: 4rem;
    transform: translate(-50%, -50%);
    z-index: 10;
    animation: none;
}

.p-wrapper {
    background-color: grey;
    border-radius: 5px;
    left: 50%;
    top:50%;
    transform: translate(-50%, -50%);
    position: absolute;
    opacity: 80%;
}
.carousel p {
    padding: 1rem;
    font-size: 1.25rem;
    line-height: 1.5;
    letter-spacing: 0.1rem;
    z-index: 10;
    text-align: left;
    animation: none;
}


.carousel a {
    position: absolute;
    text-decoration: none;
    border-radius: 5px;
    left: 50%;
    top:80%;
    transform: translate(-50%, -50%);
    z-index: 10;
    width: 200px;
    padding: 1rem;
    font-size: 2rem;
    background-color: var(--maroon);
}

.carousel a:hover {
    background-color: var(--stone-light);
}

.markers {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 50%;
    top:95%;
    transform: translate(-50%, -50%);
    gap: 1rem;
    z-index: 10;
}

.marker {
    width: 50px;
    height: 10px;
    background-color: black;
    border-radius: 5px;
    opacity: 50%;
}

.glow {
    background-color: white;
}


.arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
}

.arrow:hover {
    cursor: pointer;
    color: var(--stone-light);
}


.fa-angle-left {
    position: absolute;
    left: 5%;
}

.fa-angle-right {
    position: absolute;
    right: 5%;
}

@media only screen and (max-width: 768px) {
    .car-cont {
        position: relative;
        z-index: 10;
        width: 100%;
        left: 50%;
        margin-top: 5rem;
        margin-bottom: 5rem;
        transform: translateX(-50%);
    }

    .carousel {
        height: fit-content;
        width: fit-content;
        border-radius: 15px;
    }
    .carousel h1 {
        font-size: 1.5rem;
        margin: 0;
    }

    .carousel p {
        font-size: 1rem;
        height: 25%;
        padding: 0;
        text-align: center;
    }

    .carousel a {
        font-size: 1rem;
        width: fit-content;
    }

    .marker {
        width: 2rem;
    }
}