.home {
    margin-top: 5rem;
    padding: 1rem;
    font-family:Georgia, 'Times New Roman', Times, serif;
}

.home-wrapper {
    display: grid;
    position: relative;
    grid-template-columns: repeat(3, 1fr);
}
.star-img {
    width: 50%;
}

.leo {
    justify-self: start;
}

.aries {
    /* transform: rotate(-45deg); */
    justify-self: end;

}

.info-box {
    border-radius: 5px;
    border: 1px solid black;
    padding: 0.5rem;
    background-color: #E8DEFC;
    margin: auto;
    text-align: center;
    justify-self: stretch;
}

.introduction p {
    margin-top: 1rem;
}

.home-button {
    margin: 1rem;
    padding: 1rem;
    cursor: pointer;
    background-color: #eee8fb;
    border-radius: 5px;
    font-size: 1rem;
}

.home-button:hover {
    background-color: #E8DEFC;
}

.questionaire h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.questionaire img {
    width: 75%;
    margin-bottom: 1rem;
}

.questionaire input {
    width: 75%;
    height: 40px;
    text-align: center;
    font-size: 1rem;
}

.error-message {
    color: red;
    display: block;
    font-size: 1rem;
}

.hide-elem {
    display: none;
}

@media only screen and (max-width: 768px) {
    .home {
        margin-top: 3rem;
    }
    .home-wrapper {
        display: flex;
        flex-direction: column;
    }

    .star-img {
        width: 20%;
    }

    .aries {
        order: 1;
        align-self: center;
        margin-top: 1rem;
        margin-bottom: 2rem;
    }

    .info-box {
        order: 2;
    }
    .leo {
        margin-top: 2rem;
        order: 3;
        align-self: center;
    }

}
